<template lang="pug">
    .test__answer-item
        .test__number {{answerChar}}
        .test__answer-text
            textarea(v-model='answer' :disabled='!testEditable').test__answer
        vue-mathjax(:formula='answer' v-if='texTranslateVisible').answer__formula
</template>
<script>
import {VueMathjax} from 'vue-mathjax'
export default {
    components: {
        'vue-mathjax': VueMathjax
    },
    props: {
        answer: String,
        answerChar: String,
        texTranslateVisible: Boolean,
        testEditable: Boolean
    },
    data() {
        return {
            answerItem: '' ,
            currentTestEditable: this.testEditable  
        }
    },
    watch: {
        currentTestEditable: function () {
            document.getElementById(`answerChar${this.answer}`).removeAttribute('disabled')
            //else document.getElementById('answerChar').setAttribute('disabled',true)
        }
    }
}
</script>
<style lang="scss">
.mjx-chtml{ font-size: 100% !important; }
.mjx-math { font-size: 100% !important; }
